.wrap.list .page-title { padding: 0.18667rem 1.93333rem; line-height: 1; font-size: 18px; background: linear-gradient(90deg, #a700e4, #ef3f3e); color: #fff; border-radius: 0.42667rem; display: inline-block; width: auto; }

[data-dpr="2"] .wrap.list .page-title { font-size: 36px; }

[data-dpr="3"] .wrap.list .page-title { font-size: 54px; }

.wrap.list .panel { margin-top: 0.66667rem; }

.wrap.list .panel:first-of-child { margin-top: pxem(63); }

.wrap.list .panel .panel-title { text-align: center; height: 0.84rem; line-height: 0.84rem; }

.wrap.list .panel .panel-body { padding-top: 0.84rem; overflow: hidden; font-size: 18px; }

[data-dpr="2"] .wrap.list .panel .panel-body { font-size: 36px; }

[data-dpr="3"] .wrap.list .panel .panel-body { font-size: 54px; }

.wrap.list .panel .panel-body ul { min-height: 1.13333rem; overflow: hidden; }

.wrap.list .panel .panel-body ul > li { float: left; width: 50%; padding: 0.46667rem 0; }

.wrap.list .panel.first-panel .panel-body ul > li:last-of-type { border-left: 1px solid #3f00b2; }

.wrap.list .panel.sec-panel .panel-body ul > li { width: 33.333%; padding: 0.28rem 0; }

.wrap.list .panel.third-panel .panel-body ul > li, .wrap.list .panel.fourth-panel .panel-body ul > li { width: 20%; padding: 0.26667rem 0; font-size: 12px; }

[data-dpr="2"] .wrap.list .panel.third-panel .panel-body ul > li, [data-dpr="2"] .wrap.list .panel.fourth-panel .panel-body ul > li { font-size: 24px; }

[data-dpr="3"] .wrap.list .panel.third-panel .panel-body ul > li, [data-dpr="3"] .wrap.list .panel.fourth-panel .panel-body ul > li { font-size: 36px; }
